/* `<img>` tags are not hidden */
.f-hero img { display: block; }

.f-hero__wrapper {
	lost-center: var(--page-max-width);
}

.f-hero__wrapper_video {
	height: 0;
}

.f-hero__wrapper_big {
	padding-top: 11%;
}

.f-hero__wrapper_bigdata {
	padding-top: 0%;
}

.f-hero__heading {
	position: relative;

	/* this will look bad but won't break the mobile layout */
	overflow-x: auto; /* add scroll bar if heading is too wide to fit page */
}
.f-hero__heading::before {
	display: none;

	position: absolute;
	top: 0;
	left: -50.8rem;
	width: 50rem;
	height: 100%;

	background-color: var(--color-black);

	content: "";
}

.f-hero__heading h1 {
	display: inline;

	margin: 3rem 0 0 0;
	padding-right: 1.4rem;
	padding-left: 0.8rem;

	background-color: rgba(0, 0, 0, 0.65);

	color: var(--color-white);
	font-size: 7.7rem;
	font-weight: lighter;
}

.f-hero__heading h1:first-child {
	margin-left: 2.6rem;
}

.f-hero__heading-2 {
	margin: 0 0 0 1.6em;
	/* because of our font char padding, numbers do not add up with f-hero__text */
	padding-left: 0.3rem;

	/* this will look bad but won't break the mobile layout */
	overflow-x: auto; /* add scroll bar if heading is too wide to fit page */
}
.f-hero__heading-2[dir="rtl"] {
	margin: 0 3.4rem 0 0;
	padding-right: 0.8rem;
}
.f-hero__heading-2 h1 {
	display: inline;
	margin: 0;
	/* see https://css-tricks.com/multi-line-padded-text/
		for how to get padding to follow word-breaks (box-shadow)
	We want the following padding on each line
	padding-right: 0.8rem;
	padding-left: 0.8rem;
	*/
	padding: 0;
	box-shadow: 0.8rem 0 0 var(--color-black-transparent), -0.3rem 0 0 var(--color-black-transparent);
	box-decoration-break: clone;

	background-color: var(--color-black-transparent, rgba(0, 0, 0, 0.65));

	color: var(--color-white);
	font-size: 7.7rem;
	font-weight: lighter;
}
@media screen and (--viewport-mobile) {
	.f-hero__text[dir="rtl"] {
		margin: 0.16rem 3.4rem 0 0
	}
}

.f-hero__heading_campaign h1:first-child {
	margin-left: 0rem;
	padding-left: 0.8rem;
	position: relative;
	left: 2.6rem;
}

.f-hero__heading_trans h1 {
	background-color: transparent;
}

.f-hero__heading_swarm h1 {
	padding: 0.4rem 1.9rem 0rem 2.1rem;
}

.f-hero__heading_mobile h1 {
	display: inline-block;

	margin-top: 1.4rem;
	margin-bottom: 1rem;
	padding: 1.4rem;

	background-color: var(--color-black-transparent);
	color: var(--color-white);
 	font-size: 4.5rem;
 	font-weight: lighter;
}

.f-hero__heading_mobile h1:first-child {
	margin-left: 2.6rem;
}

.f-hero__heading_mobile h1:nth-child(2) {
	margin-left: 0;
	padding-left: 0;
}

.f-hero__heading_link a {
	color: var(--color-white);
}

.f-hero__text_splash {
	display: inline;

	font-size: 2.5rem;
	color: var(--color-white);
	font-weight: lighter;
}

.f-hero__text {
	position: relative;

	margin: 0.16rem 0 0 3.4rem;

	z-index: 1;
}

.f-hero__text::before {
	display: none;

	position: absolute;
	top: 0;
	left: -50.8rem;
	width: 50rem;
	height: 100%;
	content: "";

	background-color: var(--color-black-transparent);

	pointer-events: none;
}

.f-hero__text span {
	margin-left: -0.8rem;
	padding: 0.8rem;
	/*padding: 0.2rem .72rem 3.4rem;*/ /*#TODO:120 make the 3.4 a global variable */

	background-color: var(--color-black-transparent);

	color: var(--color-white);
	font-size: 1.8rem;
	font-weight: lighter;
	line-height: 4.1rem;
	white-space: nowrap;
}



