@import "../variables";

.f-text {}

.f-text_attention .f-text__heading {
    color: var(--color-red);
}

.f-text__heading {
    margin-top: 0rem;
    margin-bottom: 0.65rem;

    color: var(--color-grey-dark);
    font-weight: normal;
    font-size: 2rem;
}

.f-text__text {
    margin-top: 0rem;
    margin-bottom: 2.7rem;

    color: var(--color-grey-dark);
    font-weight: lighter;
    font-size: 1.6rem;
    line-height: 1.82rem;
    text-rendering: optimizeLegibility;
}

.f-text__quote {
    font-style: italic; 
    color: var(--color-grey-dark);
    font-size: 1.9rem;
    text-rendering: optimizeLegibility;
    opacity: 0.8;
    text-indent: 3rem;
}

.f-text__quote_source {
    color: var(--color-grey-dark);
    font-size: 1.1rem;
    font-weight: 100;
    text-rendering: optimizeLegibility;
    text-align: right;
    opacity: 0.8;
    margin: -1.6rem 5rem 2rem;
}

.f-text__author {
    color: var(--color-grey-dark);
    font-size: 1.1rem;
    font-weight: 100;
    text-rendering: optimizeLegibility;
    text-align: left;
    opacity: 0.8;
    margin: -1.6rem 0rem 2rem;
}

.f-text__text_small {

    color: var(--color-grey-dark);
    font-size: 1.4rem;
    text-rendering: optimizeLegibility;
    opacity: 0.8
}

.f-text__text_cuff {
    
    font-weight: bold;
}

.f-text__text a:hover {
  text-decoration: underline;
}

.f-text__list {
    line-height: 0rem;
}

@media (--viewport-mobile) {
  .f-text__text {
    font-size: 2.6rem;
    line-height: 3.12rem;
    font-weight: 300;
  }

  .f-text__heading {
    font-size: 3.7rem;
  }

  .f-text__author {
    font-size: 1.1rem;
  }

  .f-text__text_cuff {
    font-size: 2.7rem;
    line-height: 3.12rem;
  }
}