.f-data-thumb {
	width: 100%;

	padding: 1rem 2rem 1rem 2rem;
	margin: 3rem 0 0 0;

	background-color: var(--color-white);
	
	border-radius: 1rem;
	lost-center: var(--page-max-width);

	clip: none;
	display: inline-block;

	opacity: 1;

}

.f-data-thumb__text {
	margin-top: 0;

	font-size: 1.4rem;

	padding: 0;
}

.f-data-thumb__title {
	margin-top: 0;

	font-size: 1.9rem;

	padding: 0;
}
