@-webkit-keyframes shake-it-baby {
  0% {
    height: 0px;
    transform: translate(-0.5px, 0.5px) rotate(-0.1deg);
  }
  10% {
    height: 10px;
    transform: translate(0.5px, -0.5px) rotate(0.1deg);
  }
  20% {
    height: 20px;
    transform: translate(-0.5px, 0.5px) rotate(-0.1deg);
  }
  30% {
    height: 35px;
    transform: translate(0.5px, -0.5px) rotate(0.1deg);
  }
  40% {
    height: 50px;
    transform: translate(-0.5px, 0.5px) rotate(-0.1deg);
  }
  50% {
    height: 70px;
    transform: translate(0.5px, -0.5px) rotate(0.1deg);
  }
  60% {
    height: 75px;
    transform: translate(-0.5px, 0.5px) rotate(-0.1deg);
  }
  70% {
    height: 95px;
    transform: translate(0.5px, -0.5px) rotate(0.1deg);
  }
  80% {
    height: 105px;
    transform: translate(-0.5px, 0.5px) rotate(-0.1deg);
  }
  90% {
    height: 125px;
    transform: translate(0.5px, -0.5px) rotate(0.1deg);
  }
  100% {
    height: 140px;
    transform: translate(-0.5px, 0.5px) rotate(-0.1deg);
  }
}

.f-payment-form___container-print .f-payment-form___panel-body {
  animation: shake-it-baby 1s;

  overflow: hidden;
  padding: 4rem;
}

