}
.f-thumbnail-big__image img {
  height: 358.4px;
  height: 358.4px;
  height: 22.4rem;
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: 50% 50%;
     object-position: 50% 50%;
}
