@import "../variables";
.f-thumbnail {
  min-height: 50.3rem;
  min-width: 28rem;
  margin: 0 0 3.0rem 0;

  border-radius: 0.5rem;
  background-color: var(--color-white);

  border-style: solid;
  border-width: 1px;

/*  -webkit-box-shadow: -5px 6px 16px -2px var(--color-black);
    -moz-box-shadow: -5px 6px 16px -2px var(--color-black);
    box-shadow: -5px 6px 16px -2px var(--color-black);*/
}

.f-thumbnail__image {
  height: 22rem;
  width: 100%;
  margin: 0 0 0 0;
  margin-bottom: 2rem;

  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;

  object-fit: cover;
  object-position: 50% 50%;
  cursor: pointer;
  filter: grayscale(100%);
  /*TO DO: Need browser check-up*/
}


/*This makes sure the grayscale filter won't be on mobile:*/



.f-thumbnail__image:hover {
  filter: grayscale(0%);
  transition: .3s ease-in-out;
}

.f-thumbnail__info {
  margin: 0 3.4rem 0.76rem 0;
}

.f-thumbnail__image-text {
  /*Being organized text feature*/
  /*
  .f-thumbnail__image-filter {
  position: relative;

  height: 16rem;
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;

    background: var(--color-white-transparent, hsla(0, 0%, 100%, 0.8));

  filter: blur(2px);
}
  */
}


.f-thumbnail__content {
  height: 18rem;
  padding-left: var(--gutter-width);

  margin-bottom: 1rem;
  /*overflow: hidden;*/

  /*TODO: Only work in blink browsers */
  /*Set's the height of the whole content area. For longer thumbnails, this is the one.*/
  /*mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1) 69%, rgba(0, 0, 0, 0) 96%);
  mask: linear-gradient(to bottom, rgba(0, 0, 0, 1) 69%, rgba(0, 0, 0, 0) 96%);*/
}

.f-thumbnail_bigdata {

}

.f-thumbnail__heading {
  display: inline-flex;
  margin-bottom: .4rem;
  margin: 0 0 0.4rem calc(var(--gutter-width) * -1);
  padding: .68rem 1.0rem .86rem var(--gutter-width);

  color: var(--color-white);
  font-size: 1.8rem;
  font-weight: lighter;

  background-color: var(--color-black);
}

.f-thumbnail__byline {
  /*margin: 0 0 0 3.4rem;*/
  margin: 1rem 0;

  color: var(--color-grey);
  font-size: 1.5rem;
  font-weight: normal;
}

.f-thumbnail__text {
  display: block;
  /*margin: 1.08rem 0 0 3.4rem;*/
  margin: 0 0 1rem;

  color: var(--color-grey);
  font-size: 1.4rem;
  font-weight: lighter;
  line-height: 1.7rem;

/*  padding-bottom: 1.6rem;*/

  overflow: hidden;
}

.f-thumbnail__conclusion {
  font-weight: lighter;

  text-align: right;
}


.f-thumbnail__progress {
  margin: 3.4rem 0 0 3.4rem;
}

.f-thumbnail__progress__bar,
.f-thumbnail__progress__value {
  height: 0.8rem;
  border-radius: 1rem;
}

.f-thumbnail__progress__bar {
  margin-bottom: 0.5rem;

  background-color: var(--color-grey);
}

.f-thumbnail__progress__value {
  max-width: 100%;

  transition: width 1s ease 260ms;
  will-change: width;

  background-color: var(--color-red);
}

.f-thumbnail__progress__data {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  font-size: 1rem;
  white-space: nowrap;

  padding-bottom: 1.6rem;
}

.f-thumbnail__progress__data-element {
  margin: 0;

  color: var(--color-grey);
  font-weight: normal;
  line-height: 2.2rem;
}

.f-thumbnail__progress__data-element span {
  color: var(--color-grey-dark);

  font-weight: bold;
}

@media (--viewport-mobile) {
  .f-thumbnail {
    filter: none;
    font-size: 2.2rem;
    line-height: 2.3rem;
    border-left-style: none;
    border-right-style: none;
    border-top-style: none;
    border-radius: 0;
    border-color: var(--color-grey-light);
    margin-bottom: 10rem;
  }
  .f-thumbnail__conclusion {
    font-size: 4.2rem;
    color: var(--color-grey);
  }
  .f-thumbnail__image {
    filter: none;
    height: 35rem;
  }
  .f-thumbnail__text {
    color: var(--color-grey-dark);
    line-height: 3.4rem;
    font-size: 2.4rem;
    margin-top: 1rem;
    font-weight: 300;
  }
  .f-thumbnail__content {
    /*min-height: 28rem;*/
    height: auto;
  }
  .f-thumbnail__progress__data {
    font-size: 2.35rem;
    margin-top: 2rem;
  }
  .f-thumbnail__progress__data-element span {
    font-weight: 500;
  }
  .f-thumbnail__heading {
    line-height: 5rem;
    font-size: 4.4rem;
    padding: 1.36rem 2.0rem 1.12rem 3.4rem;
  }
}